.register {
    margin: 0;
    margin-bottom: 70px;
    display: grid;
    align-self: center;
    width: 396px;
    align-items: center;
    grid-template-areas: "logo logo" "welcome welcome" "title-name title-name" "name name" "title-email title-email" "email email" "title-password title-password" "password password" "error error" "button button" "text link";
    color: rgba(255, 255, 255, 1);
}

.register__logo {
    margin: 0;
    height: 38px;
    width: 38px;
    margin-top: 70px;
    margin-bottom: 40px;
    grid-area: logo;
    background: url('../../images/header-logo.svg');
}

.register__logo:hover {
    opacity: 0.5;
    transition: linear 0.5s;
}

.register__welcome {
    margin: 0;
    margin-bottom: 40px;
    grid-area: welcome;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 29px;
    letter-spacing: 0em;
    text-align: left;
}

.register__title {
    margin: 0;
    margin-bottom: 5px;
    grid-area: title-name;
    font-family: Inter;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
    letter-spacing: 0em;
    text-align: left;
    color: rgba(160, 160, 160, 1);
}

.register__title:nth-child(5) {
    grid-area: title-email;
}

.register__title:nth-child(7) {
    grid-area: title-password;
}

.register__input {
    margin: 0;
    margin-bottom: 24px;
    padding-bottom: 10px;
    grid-area: name;
    border: none;
    background: none;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 17px;
    letter-spacing: 0em;
    text-align: left;
    color: rgba(255, 255, 255, 1);
    border-bottom: 1px solid rgba(66, 66, 66, 1);
}

.register__input:focus-visible {
    border-bottom: 1px solid rgba(66, 133, 244, 1);
}
.register__input:focus {
    outline: none;
}

.register__input:nth-child(6) {
    grid-area: email;
}

.register__input:nth-child(8) {
    grid-area: password;
    margin-bottom: 10px;
}

.register__input_error {
    color: rgba(255, 48, 85, 1);
}
.register__input_error:nth-child(6) {
    color: rgba(255, 48, 85, 1);
}
.register__input_error:nth-child(8) {
    color: rgba(255, 48, 85, 1);
}

.register__input-error {
    margin: 0;
    grid-area: error;
    font-family: Inter;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
    letter-spacing: 0em;
    text-align: left;
    color: rgba(255, 48, 85, 1);
}

.register__button-login {
    margin: 0;
    margin-bottom: 16px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-area: button;
    height: 45px;
    border: none;
    border-radius: 3px;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 17px;
    letter-spacing: 0em;
    text-align: center;
    color: rgba(255, 255, 255, 1);
    background: rgba(66, 133, 244, 1);
}

.register__button-login:hover {
    opacity: 0.5;
    transition: linear 0.5s;
}

.register__button-login_unactive {
    cursor:unset;
    color: rgb(177, 166, 166);
    background: rgb(84, 84, 85);
}

.register__button-login_unactive:hover {
    opacity: 1;
    transition: none;
}

.register__text {
    margin: 0;
    width: fit-content;
    margin-left: auto;
    margin-right: 6px;
    grid-area: text;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 17px;
    letter-spacing: 0em;
    text-align: center;
    color: rgba(160, 160, 160, 1);
}

.register__link {
    margin: 0;
    cursor: pointer;
    width: fit-content;
    text-decoration: none;
    grid-area: link;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 17px;
    letter-spacing: 0em;
    text-align: center;
    color: rgba(66, 133, 244, 1);
}

.register__link:hover {
    opacity: 0.5;
    transition: linear 0.5s;
}

@media screen and (min-width: 1280px) {
    .register {
        margin-bottom: 70px;
    }
    .register__logo {
        margin-top: 70px;
    }
    .register__input-error {
        margin-bottom: 127px;
    }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
    .register__input-error {
        margin-bottom: 123px;
    }
    .register {
        margin-bottom: 243px;
    }
    .register__logo {
        margin-top: 225px;
    }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
    .register {
        margin-bottom: 30px;
        width: 260px;
    }
    .register__logo {
        justify-self: center;
        margin-top: 56px;
        margin-bottom: 50px;
    }
    .register__welcome {
        justify-self: center;
        margin-bottom: 80px;
    }
    .register__input-error {
        margin-bottom: 215px;
        font-size: 10px;
        line-height: 12px;
    }
    .register__button-login {
        margin-bottom: 14px;
        font-size: 12px;
        line-height: 15px;
    }
    .register__text {
        margin-right: 7px;
        font-size: 12px;
        line-height: 15px;
    }
    .register__link {
        font-size: 12px;
        line-height: 15px;
    }
}