.footer {
    margin: 0;
    margin-bottom: 20px;
    display: grid;
    grid-template-areas: "title title title title" "copyrigh link1 link2 link3";
    color: rgba(255, 255, 255, 1);
}

.footer__title {
    display: flex;
    justify-content: center;
    grid-area: title;
    margin: 0;
    margin-top: 79px;
    padding-bottom: 20px;
    margin-bottom: 20px;
    width: 100%;
    font-family: Inter;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0px;
    text-align: center;
    color: rgba(139, 139, 139, 1);
    border-bottom: 1px solid rgba(66, 66, 66, 1);
}

.footer__copyrigh {
    margin: 0;
    grid-area: copyrigh;
    font-family: Inter;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0px;
    text-align: left;
}

.footer__link {
    margin: 0;
    grid-area: link1;
    font-family: Inter;
    font-size: 13px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0px;
    text-align: right;
    color: rgba(255, 255, 255, 1);
    text-decoration: none;
}

.footer__link:hover {
    opacity: 0.5;
    transition: linear 0.5s;
}

.footer__link:nth-child(4) {
    grid-area: link2;
}

.footer__link:nth-child(5) {
    grid-area: link3;
}

@media screen and (min-width: 1280px) {
    .footer {
        margin-left: 70px;
        margin-right: 70px;
    }
    .footer__copyrigh {
        width: 877px;
    }
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
    .footer {
        margin-left: 30px;
        margin-right: 30px;
    }
    .footer__copyrigh {
        width: 445px;
    }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
    .footer {
        margin-left: 10px;
        margin-right: 10px;
        margin-bottom: 10px;
        grid-template-areas: "title" "link1" "link2" "link3" "copyrigh";
        justify-items: center;
    }
    .footer__copyrigh {
        width: fit-content;
        margin-top: 18px;
        font-size: 12px;
        line-height: 15px;
        color: rgba(160, 160, 160, 1);
    }
    .footer__title {
        padding-bottom: 21px;
        margin-bottom: 30px;
        font-size: 12px;
        line-height: 15px;
    }
    .footer__link {
        font-size: 12px;
        line-height: 15px;
        margin-bottom: 12px;
    }
}